import React from 'react';
import { Helmet } from 'react-helmet';
import { Button } from 'reactstrap';


const App = () => (
  <div>
    <Helmet>
      <meta charSet="utf-8" />
      <title>JIVEWORLD</title>
    </Helmet>
    <h1>Welcome to jiveworld dashboard</h1>
    <Button color="danger">Ok!</Button>
  </div>
);

export default App;



