import { call, put } from 'redux-saga/effects'
import { loginUser } from 'routines';

export function* login(data) {
    try {
        // trigger loginUser action
        yield put(loginUser.request());
        // call action to send data
        const user = yield call(() => {
            return {email: 'test@example.com'};
        });
        // if login successfully finished
        yield put(loginUser.success(user));
    } catch (error) {
        // if request failed
        yield put(loginUser.failure(error));
    } finally {
        // trigger fulfill action
        yield put(loginUser.fulfill());
    }
}



