import { loginUser } from 'routines';
import { takeEvery } from 'redux-saga/effects'
import * as AuthActions from 'actions/auth';


function* watchLoginUserSaga() {
    // run fetchDataFromServer on every trigger action
    yield takeEvery(loginUser.TRIGGER, AuthActions.login)
}

export default watchLoginUserSaga;



